import Component from 'vue-class-component'
import {Model, Prop, Vue} from 'vue-property-decorator'
import {IndicatorValue} from "@/shared/loader";
import {CriminalCharts} from "@/shared/charts";

@Component
export default class Region1975Map extends Vue {

    @Prop()
    public data!: IndicatorValue[]

    public value(valueArea: string): string {
        const result = this.data.find(item => item.area === valueArea)
        if (typeof result !== 'undefined') {
            return result.value.toLocaleString()
        }
        return this.$t('na').toString()
    }

    get areas() {
        return CriminalCharts.AREAS.filter(area => area.substr(0,4) === '1975')
    }

    public onRepublicClick() {
        this.$emit("onRepublicClick")
    }

}
